export class Cart extends React.Component{
    render(){
        function setit(eachstring){
            var ac='';
            for(var k=1;k<eachstring.length;k++){
                ac=ac+eachstring[k];
            }
            return ac;
        }
        function totalp(arr){
            var j=0;
            for(var e=0;e<arr.length;e++){
                j=j+allwears[arr[e]-1][1];
                console.log(allwears[arr[e]-1][1]);
            }
            return j;
        }
        const lij=this.props.p.map(e2 =>setit(e2));
        var price=totalp(lij);
        const li=lij.map((eachdress,i)=>(<div id={i+'total'} key={i} className='cartitem'>
            <img src={allwears[eachdress-1][0]} alt='dejf' className='cartimg'/>
            <div className='pricetag'>Rs.{allwears[eachdress-1][1]}</div>
            <div className='brandname'>{allwears[eachdress-1][2]}</div>
        </div>));
        if(document.getElementById('nof')){
            document.getElementById('nof').style.display='inline-block';
            document.getElementById('nof').innerHTML=lij.length;
        }
        if(this.props.sc===5){
        return(
            <div id='carttot' className='con'><h1 className='he'>Your Cart {price===0 && 'is Empty'}</h1>{li}
            <div id='totalprice'><div>Total(+gst):- Rs.{price} BUY NOW !</div></div>
            </div>
        );
        }
        else{
            return <div></div>;
        }
    }
}