import React, {useState} from 'react';
import './App.css';
import {Header} from './components/header.js';
import {Im} from './components/scroller';
import {Intro} from './components/intro';
import {Trust} from './components/trust';
import {Payment} from './components/payments';
import {Footer} from './components/footer';
import {ChildrenWear} from './components/child';
import {Cart} from './components/cart';
import {Accessories} from './components/accessories';
import {Wearing} from './components/mandw';
function App() {
  const [cart, setCart]=useState([]);
  const [see,setSee]=useState(1);
  const handleChange=(e)=>{
    let g=e.target.id;
    for(let i=0;i<cart.length;i++){
      if(g===cart[i]){
        setCart((prev)=>(prev.filter(de => de!==g)));
        document.getElementById(g).innerHTML='Add to Cart';
        return;
      }
    }
    setCart((prev)=>([g,...prev]));
    document.getElementById(g).innerHTML='Remove Item';
  }
  function homescreen(){
    setSee(1);
  }
  function screen2(){
    
    setSee(2);
  }
  function screen3(){
  
    setSee(3);
  }
  function screen4(){
   
    setSee(4);
  }
  function screen5(){

    setSee(5);
  }
  return (
    <div>
      <span id='top'>f</span>
      <Header handleSee1={homescreen} handleSee5={screen5} />
      {see===1 && <Im handleSee2={screen2} handleSee3={screen3} handleSee4={screen4}/>}
      
      {see===1 &&<Intro handleSee2={screen2} handleSee3={screen3} handleSee4={screen4}/>}
      {see===2 && <ChildrenWear handleChange={handleChange} p={cart}/>}
      {see===3 && <Accessories handleChange={handleChange} p={cart}/>}
      {see===4 && <Wearing handleChange={handleChange} p={cart}/>}
      <Cart p={cart} sc={see} handleSee1={homescreen}/>
      <Trust />
      <Payment />
      <Footer />
    </div>
  );
}
export default App;
