import React from 'react';
var id=null;
clearInterval(id);
id=setInterval(f,2000);
var d=0;
function f(){
    let y=['rou0','rou1','rou2','rou3'];
    let x=['-100%','-200%','-300%'];
    if(document.getElementById('img-con').style.left==='-300%'){
        document.getElementById('img-con').style.left='0';
        d=0;
        rem();
        document.getElementById(y[d]).style.backgroundColor='rgb(68, 93, 104)';
    }
    else{
        document.getElementById('img-con').style.left=x[d];
        d++;
        rem();
        document.getElementById(y[d]).style.backgroundColor='rgb(68, 93, 104)';
    }
}
function rem(){
    document.getElementById('rou0').style.backgroundColor='rgb(165, 221, 216)';
    document.getElementById('rou1').style.backgroundColor='rgb(165, 221, 216)';
    document.getElementById('rou2').style.backgroundColor='rgb(165, 221, 216)';
    document.getElementById('rou3').style.backgroundColor='rgb(165, 221, 216)';
}
function my0(){
    clearInterval(id);
    console.log('hi');
    document.getElementById('img-con').style.left='0';
    rem();
    document.getElementById('rou0').style.backgroundColor='rgb(68, 93, 104)';
}
function my1(){
    clearInterval(id);
    document.getElementById('img-con').style.left='-100%';
    rem();
    document.getElementById('rou1').style.backgroundColor='rgb(68, 93, 104)';
}
function my2(){
    clearInterval(id);
    console.log('hi');
    document.getElementById('img-con').style.left='-200%';
    rem();
    document.getElementById('rou2').style.backgroundColor='rgb(68, 93, 104)';
}
function my3(){
    clearInterval(id);
    document.getElementById('img-con').style.left='-300%';
    rem();
    document.getElementById('rou3').style.backgroundColor='rgb(68, 93, 104)';
}
export class Im extends React.Component{
    componentWillUnmount(){
        clearInterval(id);
    }
    render(){
        return(<div>
            <div id='img-scroll' className='img-s'>
                <div id='img-con' className='img-c'> 
                    <a href='#top' className='each-img' onClick={this.props.handleSee2}><img src='./images/scroll1.jpg' alt='s'/></a>
                    <a href='#top' className='each-img' onClick={this.props.handleSee3}><img src='./images/scroll2.webp' alt='shoes'/></a>
                    <a href='#top' className='each-img' onClick={this.props.handleSee4}><img src='./images/scroll3.webp' alt='mens-wear'/></a>
                    <a href='#top' className='each-img' onClick={this.props.handleSee4}><img src='./images/scroll4.webp' alt='womens'/></a>
                </div>
            </div>
            <div id='yo'>
            <span className='rou' id='rou0' onClick={my0}></span>
            <span className='rou' id='rou1' onClick={my1}></span>
            <span className='rou' id='rou2' onClick={my2}></span>
            <span className='rou' id='rou3' onClick={my3}></span>
                </div>
            </div>
        );
    }
}