import React from 'react';
export class Accessories extends React.Component{
    sn(str){
        const t=this.props.p;
        for(let i=0;i<t.length;i++){
            if(str===this.props.p[i]){
                
                return 'Remove Item';
            }
        }
        return 'Add to Cart';
    }
    render(){
        return(
            <div id='access' className='con'>
                <h1 className='he'>Accessories</h1>
                <div className='all'>
                <div className='each le'><img src='./images/dress13.webp' alt='dress1'/><h2 className='brandname'>Red Tape</h2><div className='des'>Men Walking Shoes</div><div className='pricetag'>Rs.1024</div><div className='addto' id='w13' onClick={this.props.handleChange}>{this.sn('w13')}</div></div>
                <div className='each ri'><img src='./images/dress14.webp' alt='dress2'/><h2 className='brandname'>USPA</h2><div className='des'>Men blackcolored Sneakers</div><div className='pricetag'>Rs.3012</div><div className='addto' id='w14' onClick={this.props.handleChange}>{this.sn('w14')}</div>
                </div>
            </div>
            <div className='all'>
                <div className='each le'><img src='./images/dress15.webp' alt='dress1'/><h2 className='brandname'>Sparks</h2><div className='des'>Men Sport Shoes</div><div className='pricetag'>Rs.718</div><div className='addto' id='w15' onClick={this.props.handleChange}>{this.sn('w15')}</div></div>
                <div className='each ri'><img src='./images/dress16.webp' alt='dress2'/><h2 className='brandname'>Cargi</h2><div className='des'>Womens open toe Flats</div><div className='pricetag'>Rs.2870</div><div className='addto' id='w16' onClick={this.props.handleChange}>{this.sn('w16')}</div>
                </div>
            </div>
            <div className='all'>
                <div className='each le'><img src='./images/dress17.webp' alt='dress1'/><h2 className='brandname'>Red Tape</h2><div className='des'>Indian Festi Wear</div><div className='pricetag'>Rs.470</div><div className='addto' id='w17' onClick={this.props.handleChange}>{this.sn('w17')}</div></div>
                <div className='each ri'><img src='./images/dress18.webp' alt='dress2'/><h2 className='brandname'>HRX</h2><div className='des'>Sports Cap for Men</div><div className='pricetag'>Rs.999</div><div className='addto' id='w18' onClick={this.props.handleChange}>{this.sn('w18')}</div>
                </div>
            </div>
            <div className='all'>
                <div className='each le'><img src='./images/dress19.webp' alt='dress1'/><h2 className='brandname'>ROX</h2><div className='des'>Sunglasses Men</div><div className='pricetag'>Rs.473</div><div className='addto' id='w19' onClick={this.props.handleChange}>{this.sn('w19')}</div></div>
                <div className='each ri'><img src='./images/dress20.webp' alt='dress2'/><h2 className='brandname'>Maniac</h2><div className='des'>Liquid lip Color</div><div className='pricetag'>Rs.1071</div><div className='addto' id='w20' onClick={this.props.handleChange}>{this.sn('w20')}</div>
                </div>
            </div>
            </div>
        );
    }
}