import React from 'react';
export class Payment extends React.Component{
    constructor(props){
        super(props);
        this.state={
            a: 1
        }
        this.handleChange=this.handleChange.bind(this);
    }
    handleChange(){
        this.setState({a: 0});
    }
    render(){
        return(
            <div id='pay'>
                <div className='po po1'><div className='h23'>Payment</div><img src='./images/allcards.png' alt='debitcard'/></div>
                <div className='po po2'><div className='h23'>Connect</div>
                <a href="https://www.facebook.com/yaswanth.vemireddy/" target="_blanck" ><img src='./images/facebook.svg' alt='socialnetwork'/></a>
                <a href="https://www.instagram.com/yashwanth__red_d_y" target="_blanck"><img src='./images/instagram.svg' alt='socialnetwork'/></a>
                <a href="https://www.linkedin.com/in/yashwanth-reddy-bbb657193" target='_blanck'><img src='./images/linkedin.svg' alt='socialnetwork'/></a>
                <a href='https://github.com/yashwanth-reddy909' target='_blanck'><img src='./images/github.svg' alt='socialnetwork'/></a>
                </div>
                <div className='po po3'><div className='h23'>SUBSCRIBE</div>{this.state.a ? 
                    <form onSubmit={this.handleChange}>
                    <input type='text' placeholder='Enter Mail Address' id='inputmail' />
                    <input type='submit' id='sub'/>
                    </form> : <h1 id='thank'>Thanks for letting us!</h1>}</div>
                </div>
        );
    }
}